var $boxes = $(".boxlink"),
  $productLinks = $(".NetwerkListPane .zw-global-svg").mouseover(function () {
    $boxes
      .removeClass("active")
      .filter("#NetwerkBox" + this.id)
      .addClass("active");
  });

$(document).ready(function () {
  $(".NetwerkListPane .zw-global-svg").hover(function () {
    $(".NetwerkListPane .zw-global-svg")
      .not(this)
      .removeClass("active")
      .addClass("inactive");
    $(this).addClass("active").removeClass("inactive");
  });
});

const networkSlider = new Swiper(".js-mb-ntwrk-slider", {
  slidesPerView: 1,
  grabCursor: false,
  spaceBetween: 0,
  keyboard: {
    enabled: true,
  },
  // hashNavigation: {
  //   watchState: true,
  // },
  pagination: {
    el: ".swiper-pagination",
    clickable: true,
  },
  breakpoints: {
    320: {
      slidesPerView: 1.1,
      spaceBetween: 20,
    },

    768: {
      slidesPerView: 1,
    },
  },
  navigation: {
    nextEl: ".swiper-button-next",
    prevEl: ".swiper-button-prev",
  },
});

// Mobile slider
(function () {
  "use strict";
  // breakpoint where swiper will be destroyed
  // and switches to a dual-column layout
  var breakpoint = window.matchMedia("(min-width: 992px)");
  // keep track of swiper instances to destroy later
  var mySwiper12;
  var secondSlider = mySwiper12;

  var breakpointChecker = function () {
    // if larger viewport and multi-row layout needed
    if (breakpoint.matches === true) {
      // clean up old instances and inline styles when available
      if (secondSlider !== undefined) secondSlider.destroy(true, true);
      // or/and do nothing
      return;
      // else if a small viewport and single column layout needed
    } else if (breakpoint.matches === false) {
      // fire small viewport version of swiper
      return Secondslider();
    }
  };

  var Secondslider = function () {
    secondSlider = new Swiper(".js-zetnetwork", {
      loop: false,
      slidesPerView: 1.2,
      spaceBetween: 15,
      // a11y: true,
      keyboardControl: true,
      grabCursor: true,
      breakpoints: {
        320: {
          slidesPerView: 1.2,
        },
        769: {
          slidesPerView: 2.2,
        },
      },
    });
  };

  // keep an eye on viewport size changes
  breakpoint.addListener(breakpointChecker);
  // kickstart
  breakpointChecker();
})(); /* IIFE end */
